const Works = ({ myWorks }) => {
  return (
    <>
      <section id="works" className="def-font lg:mt-32 sm:mt-16 mt-8">
        <h2 className="h2-style">Мои работы:</h2>
        <div
          id="carouselExampleControls"
          className="relative lg:h-[700px] sm:h-[400px] h-[200px] rounded-3xl"
          data-te-carousel-init
          data-te-carousel-slide
        >
          <div className="relative h-full w-full overflow-hidden after:clear-both after:block after:content-[''] rounded-3xl shadow-custom border-4 border-black">
            <a
              href="https://siestaa.github.io/liga/"
              className="relative h-full float-left -mr-[100%] w-full transition-transform duration-[600ms] ease-in-out motion-reduce:transition-none group"
              data-te-carousel-active
              data-te-carousel-item
              target="_blank"
              rel="noreferrer"
            >
              <img
                src={require('../img/work-1.jpg')}
                className="block w-full h-full object-cover object-left-top"
                alt="..."
              />
              <div className="absolute inset-x-[15%] bottom-10 hidden py-5 text-center text-black md:block z-50 bg-red-200/[.8] w-fit mx-auto px-10 rounded-full group-hover:bg-red-400/[.8] duration-500">
                <h5 className="lg:text-3xl text-xl group-hover:text-white duration-500">Smart Device</h5>
                <p className="lg:text-2xl text-lg group-hover:text-white duration-500">
                  Поставщик электрокомлектующих - главная страница .
                </p>
              </div>
            </a>
            {myWorks.map((work) => (
              <a
                key={work.id}
                href={work.href}
                className="relative h-full float-left -mr-[100%] hidden w-full transition-transform duration-[600ms] ease-in-out motion-reduce:transition-none group"
                data-te-carousel-item
                target="_blank"
                rel="noreferrer"
              >
                <img src={work.img} className="block w-full h-full object-cover object-left-top" alt="..." />
                <div className="absolute inset-x-[15%] bottom-10 hidden py-5 text-center text-black md:block z-50 bg-red-200/[.8] w-fit mx-auto px-10 rounded-full group-hover:bg-red-400/[.8] duration-500">
                  <h5 className="lg:text-3xl text-xl group-hover:text-white duration-500">{work.name}</h5>
                  <p className="lg:text-2xl text-lg group-hover:text-white duration-500">{work.abstract}</p>
                </div>
              </a>
            ))}
          </div>
          <button
            className="absolute bottom-0 left-0 top-0 z-[1] flex w-[15%] items-center justify-center group border-0 bg-none p-0 text-center text-black opacity-100 transition-opacity duration-150 ease-[cubic-bezier(0.25,0.1,0.25,1.0)] hover:text-white hover:no-underline hover:opacity-90 hover:outline-none focus:text-white focus:no-underline focus:opacity-90 focus:outline-none motion-reduce:transition-none"
            type="button"
            data-te-target="#carouselExampleControls"
            data-te-slide="prev"
          >
            <span className="inline-block lg:h-10 lg:w-10 h-6 w-6 bg-red-200/[.8] rounded-full group-hover:bg-red-400/[.8]">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="1.5"
                stroke="currentColor"
                className="lg:h-6 lg:w-6 h-3 w-3 scale-150 translate-x-[5px] translate-y-[7px]"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M15.75 19.5L8.25 12l7.5-7.5"
                />
              </svg>
            </span>
            <span className="!absolute !-m-px !h-px !w-px !overflow-hidden !whitespace-nowrap !border-0 !p-0 ![clip:rect(0,0,0,0)]">
              Previous
            </span>
          </button>
          <button
            className="absolute bottom-0 right-0 top-0 z-[1] flex w-[15%] group items-center justify-center border-0 bg-none p-0 text-center text-black opacity-100 transition-opacity duration-150 ease-[cubic-bezier(0.25,0.1,0.25,1.0)] hover:text-white hover:no-underline hover:opacity-90 hover:outline-none focus:text-white focus:no-underline focus:opacity-90 focus:outline-none motion-reduce:transition-none"
            type="button"
            data-te-target="#carouselExampleControls"
            data-te-slide="next"
          >
            <span className="inline-block lg:h-10 lg:w-10 h-6 w-6 bg-red-200/[.8] rounded-full group-hover:bg-red-400/[.8]">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="1.5"
                stroke="currentColor"
                className="lg:h-6 lg:w-6 h-3 w-3 scale-150 lg:translate-x-[10px] translate-x-[7px] translate-y-[7px]"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M8.25 4.5l7.5 7.5-7.5 7.5"
                />
              </svg>
            </span>
            <span className="!absolute !-m-px !h-px !w-px !overflow-hidden !whitespace-nowrap !border-0 !p-0 ![clip:rect(0,0,0,0)]">
              Next
            </span>
          </button>
        </div>
      </section>
    </>
  );
};

export default Works;
