const Skills = ({ skills }) => {
  return (
    <>
      <section id="skills" className="scroll-smooth def-font lg:mt-32 mt-12">
        <div className="flex flex-col items-center">
          <h2 className="h2-style">Чем я&nbsp;занимаюсь?</h2>
          <p className="lg:text-4xl sm:text-xl text-lg w-full lg:mb-12 mb-4 leading-normal self-start lg:text-start text-center">
            Разработка сайтов, как со&nbsp;стороны стилизация так,
            и&nbsp;со&nbsp;стороны функционала, стала для меня новой страницей
            в&nbsp;жизни. В&nbsp;данный момент изучаю TypeScript\Vue\Redux.
            В&nbsp;свободное время люблю читать фантастику, играть в&nbsp;игры
            и&nbsp;тискать кошку. Буду рад присоединиться к вашей команде
            и&nbsp;вместе делать лучшие веб-проекты.
          </p>
          <p className="h2-style font-semibold lg:mt-12 mt-4">Мой стек:</p>
          <ul className="flex flex-wrap justify-around w-full lg:mt-4 mt-4 gap-6 sm:gap-4">
            {skills.map((item) => (
              <li key={item.name} className="lg:mx-0 mx-2 lg:my-0 my-2">
                <figure className="flex flex-col items-center transition duration-300 ease-in-out lg:hover:scale-150 hover:scale-125">
                  <img src={item.img} width="64" height="64" alt={item.name} />
                  <figcaption>{item.name}</figcaption>
                </figure>
              </li>
            ))}
          </ul>
        </div>
      </section>
    </>
  );
};

export default Skills;
